ul {
    padding: 8px;
    font-weight: bold;
}

ul li {
    display: grid;
    margin: 5px;
}

ul li a {
    padding: 8px;
    color: black;
    border: 1px solid #117a8b;
    border-radius: 3px;
}

ul li .active {
    background-color: #117a8b;
    color: white;
    text-decoration: none;
}

ul li a:hover {
    background: #117a8b;
    color: white;
    text-decoration: none;
}

.home-icon {
    color: #ffc107;
}

.setu {
    color: red;
}

.plus {
    color: #01ff3b;
}

.faq {
    color: #ff7a86;
}

.message {
    background-color: lightblue;
    padding: 15px;
    font-size: 14px;
    font-weight: bold;
    text-align: center;
}

.menu-button {
    padding: 8px;
    border: none;
}

.menu-button:focus {
    outline: 0;
}

.menu {
    font-size: 25px;
    color: white;
}

.zone-icon {
    color: blue;
}